import { useEffect } from "react";

function useOnUnloadConfirm(active, text) {
  useEffect(() => {
    function listener(e) {
      if (active) {
        e.preventDefault(); //e.returnValue = text;

        return text;
      } else {
        return undefined;
      }
    }

    window.addEventListener("beforeunload", listener);
    return () => {
      window.removeEventListener("beforeunload", listener);
    };
  }, [text, active]);
}

export { useOnUnloadConfirm };